<template>
    <div class='gymList privateKind'>
        <div class="privateWrap">
            <div class='left'>
              <div class='text'>私教种类</div>
              <span class="num" v-if="total>0">{{total}}</span>
            </div>
            <div class='right'>
              <div class='content maginRight'>
                <div class='addBranch' @click='addbranch()'>
                    <i class="iconfont icon-btn_icon_add"></i>
                    <span>添加私教种类</span>
                </div>
              </div>
              <div class='content'>
                <div class='addBranch' @click='privateLimit()'>
                  <i class="iconfont icon-icon_setting"></i>
                  <span>私教课预约限制</span>
                </div>
              </div>
            </div>
        </div>
        <Table  :tableData="tableData" :tabhide="tabhide" :tableKey="tableKey" ref="Table" :total="total" :isPage = "isPage" :pageSize = "pageSize" :currentPage="currentPage" :isShowSelectionBtn="isShowSelectionBtn" v-on:getQueryParam = "getQueryParam"
       v-on:changeVal="changeParam" v-on:getRowdata="getRowdata"></Table>
    </div>
</template>
<script>
import Table from '../../components/Table'
import {getsession, querysession, sessionstatus, privateUp} from '../../services/service.js'
import {isApiResponseOk, setSessionStorage} from '../../utils/com.js'
export default {
  data () {
    return {
      tableData: [
        // {
        //   createTime: 1540446029939,
        //   description: "",
        //   id: "5bd1574de4b0b75ea8dc7f6f",
        //   officeId: "5ba0b36ee4b05538d2c0e039",
        //   price1: '300/节',
        //   price2: '500/节',
        //   privateSessionTypeName: "ww",
        //   status: false,
        //   time: 22,
        //   updateTime: 1540446029939,
        //   btnGroup: [{text: '详情'}, {text: '启用'}]
        // }
      ],
      tableKey: [
        {name: '课程ID', value: 'id', nor: true},
        {name: '私教类型', value: 'type', nor: true},
        {name: '课程名称', value: 'name', nor: true},
        {name: '时长', value: 'durationTime', nor: true},
        {name: '非会员价格', value: 'generalPrice', nor: true},
        {name: '会员价格', value: 'vipPrice', studentKind: true},
        {name: '启用状态', value: 'status', nor: true},
        {name: '创建时间', value: 'createTime', nor: true, width: 180},
        {name: '操作', value: 'btnGroup', btn: true}
      ],
      selectList: [],
      setInfo: '',
      isShowSelectionBtn: false,
      isPage: true,
      total: 0,
      currentPage: 1,
      pageSize: 10,
      tabhide: true,
      pageNumber: 0
    }
  },
  components: {
    Table
  },
  created () {
    this.initData()
  },
  methods: {
    // 获取查询参数
    getQueryParam (queryObj) {
    },
    changeParam (page) {
      console.log(page)
      this.currentPage = page
      this.initData()
      // console.log('获取数据', data)
    },
    // 查看私教课的详情
    async getRowdata (data) {
      if (data.index === '详情') {
        const res = await querysession(data.row.id)
        if (isApiResponseOk(res) && res.data) {
          setSessionStorage('detailInfo', res.data)
          this.$router.push({name: 'addprivatekind', query: {modify: true}})
          // console.log('获取数据成功', res)
        }
      } else if (data.index === '启用') {
        const res = await sessionstatus(data.row.id, 0)
        if (isApiResponseOk(res)) {
          // console.log('获取数据成功', res)
          this.$message({
            showClose: true,
            message: '启用成功',
            type: 'success'
          })
          this.initData()
        }
      } else if (data.index === '禁用') {
        const res = await sessionstatus(data.row.id, 1)
        if (isApiResponseOk(res)) {
          // console.log('获取数据成功', res)
          this.$message({
            showClose: true,
            message: '禁用成功',
            type: 'success'
          })
          this.initData()
        }
      } else if (data.index === '置顶') {
        const res = await privateUp(data.row.id)
        if (isApiResponseOk(res)) {
          // console.log('获取数据成功', res)
          this.$message({
            showClose: true,
            message: '置顶成功',
            type: 'success'
          })
          this.initData()
        }
      }
    },
    // 添加分店
    addbranch () {
      this.$router.push({name: 'addprivatekind'})
    },
    privateLimit () {
      this.$router.push({name: 'privateLimit'})
    },
    // 获取私教课信息
    async initData () {
      const res = await getsession(this.currentPage, this.pageSize)
      if (isApiResponseOk(res)) {
        if (res.data) {
          this.total = res.data.totalNum
          this.tableData = res.data.record
          for (let val of res.data.record) {
            if (val.status == 0) {
              val['btnGroup'] = [{text: '详情'}, {text: '禁用', style: 'color: #F44A6C;'}, {text: '置顶'}]
            } else {
              val['btnGroup'] = [{text: '详情'}, {text: '启用'}]
            }
          }
          if (this.total === 0) {
            this.isPage = false
          } else {
            this.isPage = true
          }
        }
      } else {
        this.isPage = false
      }
    }
  }
}
</script>
<style lang="sass" scoped>
.gymList
    .privateWrap
      display: flex;
      justify-content: space-between;
      height: 0.5rem;
      line-height: 0.5rem;
      align-items: center;
      width: 100%;
      .left
        display: flex;
        align-items: center;
        .num
          background: #E9EDEF;
          border-radius: 0.15rem;
          font-size: 0.2rem;
          color: #8597A1;
          text-align: center;
          line-height: 0.26rem;
          padding: 0.03rem 0.12rem;
      .right
        display: flex;  
        align-items: center;
        height: 100%;
        .maginRight
          margin-right: 0.2rem;
    .text
        font-size: .24rem;
        color: #263238;
        margin-right: 0.1rem;
    .content
        display: flex;
        align-items: center;
        height: .36rem;
        .context
            color: #8597A1;
            font-size: .22rem;
            flex: 2;
        .addBranch
            border: 1px solid #20C6BA;
            border-radius: 2px;
            width: 1.6rem;
            height: .36rem;
            border-radius: .04rem;
            font-size: .15rem;
            color: #20C6BA;
            text-align: center;
            line-height: .36rem;
            cursor: pointer;
            img
                width: .12rem;
</style>




